.user_list {
  max-height: 80vh;
  overflow: auto;
}
.user_item {
  display: flex;
}
.user_item .user_name {
  width: 20vw;
  display: flex;
  align-items: center;
  cursor: pointer;
}
.user_item .user_name > div {
  margin-left: 15px;
}
.user_item .user_name > div .name {
  font-weight: bold;
}
.user_item .user_name > div .position {
  color: #333;
}
.user_item .user_name img {
  height: 50px;
  width: 50px;
  border-radius: 100%;
}
.user_item .user_name p {
  margin: 0;
}
.user_item .ability {
  display: flex;
  width: 30vw;
  flex-wrap: wrap;
}
.user_item .ability .ability_item {
  background: #eee;
  margin: 6px 10px;
  padding: 0 5px;
  display: flex;
  align-items: center;
  height: 26px;
}
.user_item .ability .ability_item .name {
  color: #ea973d;
  font-weight: bold;
}
.user_item .action {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex: 1 1;
}
