.label {
  flex-flow: wrap;
  justify-content: space-around;
  display: flex;
}
.label-box {
  display: inline-block;
  width: 200px;
  background-color: #fff;
  border: 1px solid #f1f1f1;
  -webkit-transition: border-color 0.3s;
  transition: border-color 0.3s;
  text-align: center;
  padding: 10px;
  margin: 10px 0;
}
.label-box .meta-box {
  padding: 10px;
}
.label-box .meta-box span {
  display: inline-block;
  color: #737373;
}
.label-box .label-title {
  font-size: 18px;
  line-height: 20px;
  color: #333;
}
.ant-modal-body {
  height: 500px !important;
}
