.user_info_head {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.user_info_head .left {
  display: flex;
}
.user_info_head .left img {
  height: 60px;
  width: 60px;
  border-radius: 100%;
  margin-right: 1em;
  object-fit: cover;
}
.user_info_head .left .txt_des {
  display: flex;
  flex-direction: column;
}
.user_info_head .left .txt_des p {
  margin-bottom: 0.2em;
}
.user_info_edit {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
.user_info_edit div {
  margin-top: 4px;
}
.user_info_edit div span {
  margin: 0 4px;
}
.uer_license {
  display: flex;
  justify-content: space-between;
  cursor: pointer;
  padding: 10px 60px;
  color: #ea973d;
}
.uer_license:hover {
  background: #EEE;
}
.uer_license .uer_license_time {
  color: #4d4d4d;
}
.chart {
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.chart img {
  height: 280px;
  width: 310px;
}
.chart .ability_bar {
  cursor: pointer;
  margin-bottom: 2px;
  border-radius: 5px;
  padding: 5px;
}
.chart .ability_bar:hover {
  background: #EEE;
}
.chart .bar {
  padding-left: 8vw;
  flex: 1 1;
}
.profile_right .ant-divider-horizontal {
  margin: 10px 0;
}
.profile_right .achievement p span {
  display: inline-block;
  width: 50%;
}
.profile_right .follow {
  border: 1px solid #EEE;
  padding: 20px;
  margin-top: 30px;
  display: flex;
  align-items: center;
}
.profile_right .follow .ant-divider-vertical {
  height: 3em;
}
.profile_right .follow div.content {
  flex: 1 1;
  text-align: center;
}
.experience {
  width: 100%;
  display: flex;
}
.experience h3 {
  color: #666;
}
.experience > div {
  margin: 0 5%;
  flex: 1 1;
}
.experience p {
  display: flex;
  justify-content: space-between;
}
.experience p span {
  color: #999;
}
.experience p span:first-child {
  color: #333;
}
.ant-modal-body {
  height: 400px;
  overflow: auto;
}
.ant-modal-body .ant-divider-horizontal {
  margin: 0;
}
.block p {
  margin-top: 20px;
}
.block p * {
  display: inline-block;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.block p a,
.block p .not_on_chain {
  text-align: center;
  margin: 0;
  white-space: nowrap;
  overflow: hidden;
  width: 45%;
}
.block p span.content {
  margin-bottom: 0;
  width: 30%;
  color: #666;
  font-weight: bolder;
}
.block p span {
  margin-left: 5px;
  width: 140px;
}
.loading {
  text-align: center;
  margin-top: 30px;
}
#mountNode {
  padding-left: 3vw;
  width: 30vw;
}
