.course_item {
  padding: 10px 15px;
  display: flex;
  align-items: center;
}
.course_item .left {
  flex: 1 1;
  padding-right: 20px;
}
.course_item .left .course_top {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
}
.course_item .left .course_top .course_author {
  font-size: 16px;
  font-weight: bold;
}
.course_item .left .course_top .classify {
  margin: 0 15px 0;
}
.course_item .left .course_top .start_time {
  margin: 0;
  margin-left: 15px;
}
.course_item .left .course_title {
  max-width: 640px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.course_item .left .course_content {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
.course_item .left .course_bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.course_item .left .course_bottom .course_progress .ant-progress-line {
  width: 350px;
}
.course_item img.course_cover {
  width: 120px;
  height: 100%;
}
