.course_detail {
  display: flex;
}
.course_detail .left {
  width: 270px;
}
.course_detail .left .buy {
  height: 50px;
  width: 100%;
  color: #fff;
  font-size: 18px;
  font-weight: bolder;
  cursor: pointer;
  text-align: center;
  margin-bottom: 24px;
}
.course_detail .center {
  flex: 1 1;
  margin: 0 16px;
}
.course_detail .right {
  width: 300px;
}
.course_detail .right .task_item {
  display: flex;
  justify-content: space-between;
  margin: 5px 0 5px;
}
.course_buy_protocol {
  margin: 20px;
}
.course_buy_protocol div {
  margin: 5px 0;
}
.course_buy_protocol .tip {
  color: red;
  font-size: 12px;
}
