.ant-list-item {
  display: flex;
}
.ant-list-item > div:last-child {
  flex: 1 1;
}
.ant-list-item .ant-list-item-meta {
  flex: unset;
}
.ant-list-item .name {
  display: flex;
  align-items: center;
}
.ant-list-item .name .ant-badge {
  margin: 0 8px;
}
.ant-list-item .name .ant-badge sup {
  font-size: 12px;
}
.ant-list-item .ant-avatar-image {
  height: 50px;
  width: 50px;
  border: 1px solid #ddd;
}
